import { createRouter, createWebHashHistory } from 'vue-router'
import Index from '../views/Index.vue'

const routes = [
  {
    path: '/',
    component: Index,
    meta: {
      title: 'Estiag',
    }
  },
  {
    path: '/about',
    meta: {
      title: '关于',
    },
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    path: '/aiueo',
    meta: {
      title: '五十音测试',
    },
    component: () => import('../views/Aiueo.vue')
  },
  {
    path: '/random',
    meta: {
      title: '随机数',
    },
    component: () => import('../views/Random.vue')
  },
  {
    //Vue2的404页面必须这样写 https://router.vuejs.org/guide/migration/
    path: '/:pathMatch(.*)',
    meta: {
      title: '404',
    },
    redirect: '/'
  },
  {
    path: '/jsonedit',
    meta: {
      title: 'JSON编辑',
    },
    component: () => import('../views/JsonEdit.vue')
  },
  {
    path: '/webTimmer',
    meta: {
      title: '计时器',
    },
    component: () => import('../views/WebTimmer.vue')
  },
  {
    path: '/kinship',
    meta: {
      title: '亲族关系',
    },
    component: () => import('../views/Kinship.vue')
  },
  {
    path: '/tomato',
    meta: {
      title: '番茄工作法助手',
    },
    component: () => import('../views/TomatoWork.vue')
  },
  {
    path: '/variance',
    meta: {
      title: '样本方差与全体方差',
    },
    component: () => import('../views/Variance.vue')
  },
  {
    path: '/markPhoto',
    meta: {
      title: '水印相机',
    },
    component: () => import('../views/MarkPhoto.vue')
  },
  {
    path: '/penbeatPlayer',
    meta: {
      title: 'PenBeat',
    },
    component: () => import('../views/PenbeatPlayer.vue')
  },
  {
    path: '/calorie',
    meta: {
      title: '卡路里',
    },
    component: () => import('../views/Calorie.vue')
  },
  {
    path: '/drag',
    meta: {
      title: '拖拽',
    },
    component: () => import('../views/Drag.vue')
  },
  {
    path: '/engineeringCalc',
    meta: {
      title: '工程计算',
    },
    component: () => import('../views/EngineeringCalc.vue')
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})
router.beforeEach(async (to) => {
  if (to.meta.title) { // 判断是否有标题
    document.title = to.meta.title;
  }
});
export default router
