<template>
	<div>
		<!--  是否显示导航栏  -->
		<nav v-show="isShowNav">
			<router-link to="/">主页</router-link>
			<router-link to="/about">关于</router-link>
			<select class="form-control" v-model="theme">
				<option value="light">亮</option>
				<option value="dark">黑</option>
			</select>
		</nav>
		<router-view></router-view>
	</div>
</template>
<script>
	export default {
		data: function() {
			return {
				isShowNav: true,
				theme: 'light'
			}
		},
		watch: {
			$route: function() {
				if (this.$route.path == '/health') {
					console.log('hc page hide nav')
					//隐藏导航栏
					this.isShowNav = false;
				} else {
					this.isShowNav = true;
				}
			},
			theme: function() {
				document.documentElement.setAttribute('user-theme', this.theme);
				localStorage.setItem('theme', this.theme);
			}
		},
		mounted: function() {
			if (localStorage.getItem('theme')) {
				document.documentElement.setAttribute('user-theme', localStorage.getItem('theme'));
				this.theme = localStorage.getItem('theme');
			}
		},
		methods: {}
	}
</script>
<style scoped>
	/* 这样写即使style上有scoped也是全局样式 */
	@import './assets/plugins/playground/css/style.css';
	/* 图标只需要引入这一个文件，css中引入ttf文件的url需要./开头 */
	@import './assets/iconfont/iconfont.css';

	#app {
		text-align: center;
	}

	.form-control {
		width: unset;
		float: right;
		vertical-align: middle;
		margin-top: 13px;
	}
</style>
