<template>
	<div>
		<div class="wrapper" v-bind:key="index" v-for="(item,index) in option.data" @click="jump(item.routePath)">
			<div class="card">
				<span class="icon iconfont" v-html="item.icon"></span>
			</div>
			<span class="title">{{item.title}}</span>
		</div>
	</div>
</template>

<script>
	export default {
		props: {
			option: Object,
		},
		methods: {
			jump: function(routePath) {
				//通过this.$router可以在任何组件中访问路由
				this.$router.push(routePath)
			},
		},
	}
</script>

